@media screen and (max-width: 1140px) {
  .container {
    width: 100vw;
    padding: 12px 33px;
    .header {
      justify-content: space-between;
    }
  }

  .cloud {
    width: 229px;
  }

  .logo {
    width: 183px;
  }
  .navigation {
    width: 300px;
    padding-top: 21px;
    padding-left: 39px;
    margin: 0;
    .itemNav {
      &:not(:last-child) {
        margin-right: 11px;
      }
      font-size: 18px;
    }
  }
  .title {
    &:not(:last-child) {
      margin: 0;
    }
    text-align: center;
    font-size: 25px;
    margin: 0;
  }

  .descriptionText {
    margin: 16px 0 20px 0;
    width: 339px;
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
  }

  .blueBox {
    width: 250px;
    border-radius: 20px;
    background: #7fb3e1;
    padding: 25px 0 18px 25px;
  }
  ul {
    list-style-type: none; /* Вимикаємо стандартні маркери списку */
    padding-left: 0; /* Вирівнюємо текст по лівому краю */
    margin: 0;
  }

  .blueDes {
    &:first-child {
      margin-bottom: 19px;
      width: 100%;
    }
    &:nth-child(2) {
      margin-bottom: 16px;
    }
    &:nth-child(3) {
      width: 100%;
    }
    font-size: 18px;
  }

  .blueDes::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
  }
  .boxCat {
    margin-top: 83px;
    position: relative;
    z-index: 1;
  }
  .cat {
    width: 510px;
    top: -50px;
    left: -70px;
  }

  .order {
    width: 190px;
    padding: 23px 15px;
    margin-left: 433px;
    margin-top: 2px;
  }

  .rectangle {
    width: 190px;
    height: 150px;
  }

  .timer {
    margin-top: 8px;
    font-size: 26px;
  }
  .titleTime {
    font-size: 23px;
  }
  .price {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    position: relative;
    z-index: 10;
  }
  .noprice {
    margin: 0 20px 0 0;
    font-size: 20px;
  }
  .noprice::after {
    top: 22px;
    left: -10px;
    width: 75px;
    transform: rotate(347deg);
  }
  .actprice {
    margin: 0;
    font-size: 26px;
  }

  .formIn {
    margin-top: 40px;
    margin-bottom: 10px;
  }

  .inputWrapper {
    width: 100%;
  }

  .inName {
    width: 100%; /* Ширина інпуту адаптується до ширини обгортки */
    height: 30px;
    border-radius: 25px;
    padding-left: 39px;
    font-size: 12px;
  }

  .inputWrapper::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 17px;
    height: 14px;
    width: 14px;
  }
  .btnOrder {
    width: 100%;
    height: 30px;
    margin-top: 15px;
    font-size: 20px;
  }
  .orderDate {
    margin-top: 8px;
    font-size: 15px;
  }
}

